import React, { useState, useEffect, useReducer } from "react"
import axios from "axios"
import LinkCard from "../components/LinkCards"
import { GridList, GridListTile, makeStyles, useMediaQuery, useTheme } from "@material-ui/core"
import EditFormDialog from "./EditFormDialog"
import { ACTIONS, CARD_TYPE } from "../pages/Folder"
import Notifier from "./Snackbar"
import { useNews, usePhotos, useVideos, useCovidUpdate } from "../services/LinkLibraryServices"



const useStyles = makeStyles(theme => ({
  gridListHolder: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',

  },
  gridList: {
    flexWrap: "nowrap",
    padding: theme.spacing(0,2,1,2),
  },
  discoverHeader: {
    padding: theme.spacing(0,2)
  },
  containerWrapper: {
    margin: theme.spacing(1,0)
  },
  covidGridListWrapper: {
    margin: theme.spacing(0,0,0,19),
  }
}))

export const DISCOVER_ACTIONS = {
  ADD_TO_LIBRARY: "add-to-library",
  SAVE_TO_LIBRARY: "save-to-library"
}

const discoverDispatch = (state, action) => {
  switch (action.type) {
    case DISCOVER_ACTIONS.ADD_TO_LIBRARY: {
      return {
        ...state,
        showForm: true,
        clickedLinkData: action.payload.data
      }
    }
    case ACTIONS.ADD_LINK: {
      return {
        ...state,
        showForm: false,
        openSnackbar: true,
        snackbarMessage: action.payload.message
      }
    }
    case ACTIONS.CLOSE_EDIT_FORM: {
      return {
        ...state,
        showForm: false
      }
    }
    case ACTIONS.CLOSE_SNACKBAR: {
      return {
        ...state,
        openSnackbar: false
      }
    }
    case ACTIONS.COPY_LINK: {
      return {
        ...state,
        openSnackbar: true,
        snackbarMessage: action.payload.message,
      }
    }
    default: return state
  }
}

const initialState = {
  showForm: false,
  openSnackbar: false,
  snackbarUndoButton: false,
  snackbarMessage: "",
  clickedLinkData: {}
}

const DiscoverProvider = ({ type, query }) => {

  const classes = useStyles()
  const [state, dispatch] = useReducer(discoverDispatch, initialState)

  const articles = useNews(query)
  const articleCards = articles.map(article => (
    <GridListTile key={article.url}>
      <LinkCard
        data={{
          link_title: article.title,
          link_url: article.url,
          link_img: article.image
        }}
        type={CARD_TYPE.DISCOVER_NEWS}
        dispatch={dispatch}
      />
    </GridListTile>
  ))

  const photos = usePhotos()
  const photoCards = photos.map(photo => (
    <GridListTile key={photo.id}>
      <LinkCard
        data={{
          link_title: "Photo by " + photo.photographer + " (Pexels)",
          link_url: photo.url,
          link_img: photo.src.portrait
        }}
        type={CARD_TYPE.DISCOVER_PHOTOS}
        dispatch={dispatch}
      />
    </GridListTile>
  ))

  const covidData = useCovidUpdate()
  console.log({covidData})

  const covid = covidData.map(info => (
    <GridListTile key={info["Country_text"]}>
      <LinkCard
        data={{
          link_title: info["Country_text"],
          link_img: "None",
          stats: info
        }}
        type={CARD_TYPE.COVID_UPDATE}
        dispatch={dispatch}
      />
    </GridListTile>
  ))


  const covidCards = [
    <GridListTile key={"global"}>
      <LinkCard
        data={{
          link_title: "World",
          link_img: "None",
          stats: covidData.Global
        }}
        type={CARD_TYPE.COVID_UPDATE}
        dispatch={dispatch}
      />
    </GridListTile>
    ,
    <GridListTile key={"ph"}>
      <LinkCard
        data={{
          link_title: "World",
          link_img: "None",
          stats: covidData.Global
        }}
        type={CARD_TYPE.COVID_UPDATE}
        dispatch={dispatch}
      />
    </GridListTile>
  ]

  const useGridSize = () => {
    const theme = useTheme()
    const lg = useMediaQuery(theme.breakpoints.up("lg"))
    const md = useMediaQuery(theme.breakpoints.up("md"))
    const sm = useMediaQuery(theme.breakpoints.up("sm"))

    switch (type) {
      case CARD_TYPE.DISCOVER_PHOTOS: {
        if (lg) return 8.1
        else if (md) return 6.1
        else if (sm) return 4.1
        else return 2.1
      }
      case CARD_TYPE.COVID_UPDATE: {
        if (lg) return 7
        else if (md) return 5
        else if (sm) return 3
        else return 1
      }
      default: {
        if (lg) return 7.75
        else if (md) return 5.75
        else if (sm) return 3.75
        else return 1.75
      }
    }
  }

  const getCards = () => {
    switch (type) {
      case CARD_TYPE.DISCOVER_NEWS: return articleCards
      case CARD_TYPE.DISCOVER_PHOTOS: return photoCards
      case CARD_TYPE.COVID_UPDATE: return covid
      default: return
    }
  }

  return (
    <>
      <div className={type === CARD_TYPE.COVID_UPDATE ? classes.covidGridListWrapper : classes.containerWrapper} >
        <div className={classes.gridListHolder} >
          <GridList className={classes.gridList} cols={useGridSize()} cellHeight={"auto"} spacing={8}>
            {getCards()}
          </GridList>
        </div>
      </div>
      <Notifier 
        dispatch={dispatch}
        open={state.openSnackbar}
        message={state.snackbarMessage}
        undoButton={state.snackbarUndoButton}
      />
      <EditFormDialog mode={"link"} from={"discover"}
        dispatch={dispatch} edit={state.showForm} {...state.clickedLinkData} />
    </>
  )
}

export default DiscoverProvider