import React from "react"
import { makeStyles } from "@material-ui/core/styles"
import { Card,
	CardActionArea,
	CardActions,
	CardContent,
	CardMedia,
	Typography,
	CircularProgress,
} from "@material-ui/core"
import DeleteIcon from "@material-ui/icons/Delete"
import EditIcon from "@material-ui/icons/Edit"
import LinkIcon from '@material-ui/icons/Link'
import LaunchIcon from '@material-ui/icons/Launch'
import IconButton from "@material-ui/core/IconButton"
import AddIcon from '@material-ui/icons/Add'
import ArchiveIcon from '@material-ui/icons/Archive'
import UnarchiveIcon from '@material-ui/icons/Unarchive'

import { CopyToClipboard } from 'react-copy-to-clipboard'
import { ACTIONS, CARD_TYPE } from "../pages/Folder.js"
import { DISCOVER_ACTIONS } from "./DiscoverProvider"
import { useCovidUpdate } from "../services/LinkLibraryServices.js"

const useStyles = makeStyles(theme => ({
	cardContainer: {
		background: theme.palette.background.paper,
		width: "100%",
		height: "100%",
		borderColor: theme.palette.primary.light,
		borderRadius: theme.spacing(1),
	},
	cardContent: {
		lineHeight: 1.25,
		textOverflow: "ellipsis",
	}
}))

const LinkCard = ({ dispatch, data, type,...props }) => {

	const classes = useStyles()
	let hostName = type !== CARD_TYPE.COVID_UPDATE ? getHostName(data.link_url) : ""
	const linkDetails = {
		link_title: data.link_title,
		link_url: data.link_url,
		folder_id: data.folder_id,
		link_img: data.link_img,
		date_added: data.date_added,
		is_archived: data.is_archived
	}


	const getCardMenuItems = () => {

		const archiveButton 
			= <>
				<IconButton size="small" color="primary" 
					onClick={() => dispatch({
						type: ACTIONS.ARCHIVE_LINK,
						payload: { id: data.link_id, data: { is_archived: true } }
					})}>
					<ArchiveIcon />
				</IconButton>
			</>

		const unarchiveButton
			= <>
				<IconButton size="small" color="primary" 
					onClick={() => dispatch({
						type: ACTIONS.UNARCHIVE_LINK,
						payload: { id: data.link_id, data: { is_archived: false } }
					})}>
					<UnarchiveIcon />
				</IconButton>
			</>

		const editButton
			= <>
				<IconButton size="small" color="primary"
					onClick={() => dispatch({ 
						type: ACTIONS.EDIT_LINK,
						payload: { data: { docId: data.link_id, ...linkDetails } }
					})}
				>
					<EditIcon />
				</IconButton>
			</>

		const deleteButton
			= <>
				<IconButton size="small" color="primary"
					onClick={() => dispatch({
						type: ACTIONS.DELETE_LINK,
						payload: { id: data.link_id, data: linkDetails }
					})}
				>
					<DeleteIcon />
				</IconButton>
			</>

		const addToLibraryButton
			= <>
				<IconButton size="small" color="primary"
					onClick={() => dispatch({ 
						type: DISCOVER_ACTIONS.ADD_TO_LIBRARY,
						payload: { data: { ...data, folder_id: "3ppmoaOw6ANJk10oWmhh" } }
					})}
				>
					<AddIcon />
				</IconButton>
			</>
		
		const defaultButtons
			= <>
				<CopyToClipboard text={data.link_url} 
					onCopy={() => dispatch({ 
						type: ACTIONS.COPY_LINK, 
						payload: { message: "Link URL copied to clipboard" } 
					})}
				>
					<IconButton size="small" color="primary">
						<LinkIcon />
					</IconButton>
				</CopyToClipboard>
				<IconButton size="small" color="primary"
					onClick={e => {
						e.preventDefault()
						window.open(data.link_url, "_blank")
					}}
				>
					<LaunchIcon />
				</IconButton>
			</>

		return (
			type === CARD_TYPE.COVID_UPDATE
				? ""
				: (
					<CardActions disableSpacing >
						{type === CARD_TYPE.COVID_UPDATE
							? <></>
							: type === CARD_TYPE.REGULAR
								? ( <>
									{data.is_archived === true
										? unarchiveButton
										: archiveButton
									}
									{editButton}
									{deleteButton}
									{defaultButtons}
								</> ) : ( <>
									{addToLibraryButton}
									{defaultButtons}
								</>)
						}
					</CardActions>
				)
			
		)
	}

	const getCardMedia = () => {

		const mediaHeight = type === CARD_TYPE.DISCOVER_NEWS ? 120 : "auto"

		if (data.link_img !== "None") {
			return (
				<CardMedia
					component="img"
					alt={data.link_img}
					image={data.link_img}
					title={data.link_title}
					height={mediaHeight}
				/> 
			)
		}
	}

	const getCardContent = () => {

		switch (type) {
			case CARD_TYPE.REGULAR:
			case CARD_TYPE.DISCOVER_NEWS: {
				return (
					<CardContent>
						<Typography variant="overline">
							{hostName}
						</Typography>
						<Typography className={classes.cardContent} variant="subtitle1" gutterBottom>
							{data.link_title}
						</Typography>
						<Typography noWrap className={classes.cardContent} variant="caption" gutterBottom>
							{data.link_url}
						</Typography>
					</CardContent>
				)
			}
			case CARD_TYPE.COVID_UPDATE: {
				return (
					<CardContent>
						{data.stats === undefined
							? ( 
								<div style={{display: "flex", justify: "center"}}>
									<CircularProgress
										color={JSON.parse(localStorage.getItem("theme")).value === false ? "primary" : "secondary"} 
										size={80} thickness={4.5}
									/>
								</div>
							) : (<>
								<Typography className={classes.cardContent} variant="subtitle1" gutterBottom>
									{data.link_title}
								</Typography>
								<Typography className={classes.cardContent} variant="subtitle2" gutterBottom>
									Active cases: {data.stats["Active Cases_text"]}<br/>
									New confirmed: {data.stats["New Cases_text"]}<br/>
									New deaths: {data.stats["New Deaths_text"]}<br/>
									Total confirmed: {data.stats["Total Cases_text"]}<br/>
									Total deaths: {data.stats["Total Deaths_text"]}<br/>
									Total recovered: {data.stats["Total Recovered_text"]}
								</Typography>
								<Typography  className={classes.cardContent} variant="overline" gutterBottom>
									As of {data.stats["Last Update"]}
								</Typography>
							</>)
						}
						
					</CardContent>
				)
			}
			default: return
		}
	}

	return (
		<Card variant="outlined" className={classes.cardContainer} >
			<CardActionArea>
				{getCardMedia()}
				{getCardContent()}
			</CardActionArea>
			{getCardMenuItems()}
		</Card>
	)
}

const getHostName = url => {
	const matches = url.match(/^https?:\/\/(www.)?([^/?#]+)(?:[/?#]|$)/i)
	return matches && matches[2]
}

export default LinkCard