import React, {  } from "react"
import { makeStyles, Chip, Typography } from "@material-ui/core"
import DiscoverProvider from "../components/DiscoverProvider"
import covidImg from "../img/covid.png"

import { CARD_TYPE } from "./Folder.js"

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    margin: theme.spacing(10,0)
  },
  gridListHolder: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',

  },
  gridList: {
    flexWrap: "nowrap",
    padding: theme.spacing(0,1)
  },
  chipContainer: {
    display: "flex",
    overflow: "scroll",
    padding: theme.spacing(0,2,0)
  },
  chip: {
    marginRight: theme.spacing(0.5)
  },
  discoverHeader: {
    padding: theme.spacing(0,2)
  },
  covidInfo: {
    background: `url("${covidImg}")`, 
    backgroundSize: "contain",
    backgroundRepeat: "no-repeat",
    backgroundColor: "rgba(255,182,172,0.75)",
    paddingTop: theme.spacing(1),
    marginBottom: theme.spacing(2)
  },
}))

const Discover = ({ dispatch, ...props }) => {

  const classes = useStyles()

  const chipData = [
    { key: 0, label: 'Philippines', query: "latest-news?country=ph&" },
    { key: 1, label: 'World', query: "latest-news?language=en&"},
    { key: 2, label: 'Politics', query: "search?category=politics&" },
    { key: 3, label: 'Technology', query: "search?category=technology&" },
    { key: 4, label: 'Science', query: "search?category=science&" },
    { key: 5, label: 'Gadgets', query: "search?category=gadgets&" },
    { key: 6, label: 'Culture', query: "search?category=culture&" },
  ]

  const [newsFeedQuery, setNewsFeedQuery] = React.useState("latest-news?country=ph&")
  const [activeNewsFeed, setActiveNewsFeed] = React.useState(0)

  const chips = chipData.map(chip => {
    return (
      <Chip key={chip.key} variant={activeNewsFeed === chip.key ? "default" : "outlined"}
        color={JSON.parse(localStorage.getItem("theme")).value === false ? "primary" : "secondary"} 
        className={classes.chip} label={chip.label} 
        onClick={() => {
          setActiveNewsFeed(chip.key)
          setNewsFeedQuery(chip.query)
        }}
      />
    )
  })

  const getNewsFeed = <DiscoverProvider type={CARD_TYPE.DISCOVER_NEWS} query={newsFeedQuery} />
  const photoFeed = <DiscoverProvider type={CARD_TYPE.DISCOVER_PHOTOS} query="" />
  const covidUpdate = <DiscoverProvider type={CARD_TYPE.COVID_UPDATE} query="" />

  return (
    <>
      <div className={classes.root} >
        <div className={classes.covidInfo}>
          <div className={classes.discoverHeader} >
            <Typography variant="h6" gutterBottom>
              Covid-19 Update
            </Typography>
          </div>
          {covidUpdate}
        </div>
        <div>
          <div className={classes.discoverHeader} >
            <Typography variant="h6" gutterBottom>
              Articles
            </Typography>
          </div>
          <div className={classes.chipContainer} >
            {chips}
          </div>
          {getNewsFeed}
        </div>
        <div>
          <div className={classes.discoverHeader} >
            <Typography variant="h6" gutterBottom>
              Photos
            </Typography>
          </div>
          {photoFeed}
        </div>
        {/* <DiscoverProvider
          title={"World News"}
          query={"latest-news?language=en&"}
        />
        <DiscoverProvider
          title={"Politics"}
          query={"search?category=politics&"}
        />
        <DiscoverProvider
          title={"Technology"}
          query={"search?category=technology&"}
        />
        <DiscoverProvider
          title={"Gadgets"}
          query={"search?category=gadgets&"}
        />
        <DiscoverProvider
          title={"Science"}
          query={"search?category=science&"}
        /> */}
        {/* <DiscoverProvider 
          title={"Philippines Headlines"}
          query={"everything?domains=rappler.com,philstar.com,abs-cbn.com,bworldonline.com,interaksyon.com&pageSize=30"}
        />
        <DiscoverProvider 
          title={"World Headlines"}
          query={"top-headlines?sources=al-jazeera-english,bbc-news,cnn,associated-press,reuters,time&pageSize=30"} 
        />
        <DiscoverProvider 
          title={"Coronavirus update"}
          query={"everything?q=covid-19+coronavirus"} 
        />
        <DiscoverProvider 
          title={"News analysis and opinion"}
          query={"top-headlines?sources=politico,axios,vice-news"} 
        />
        <DiscoverProvider
          title={"Science and Technology"}
          query={"top-headlines?sources=engadget,hacker-news,the-verge,techradar,techcrunch,recode,national-geographic,ign,new-scientist&pageSize=30"} 
        />
        <DiscoverProvider
          title={"Culture and entertainment"}
          query={"top-headlines?sources=buzzfeed,mashable,mtv-news"} 
        /> */}
      </div>
    </>
  )
}

export default Discover